@import url(https://fonts.googleapis.com/css2?family=Poor+Story&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poor+Story&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poor+Story&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poor+Story&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poor+Story&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poor+Story&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu',
    'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.app{position:relative;width:100%;height:100vh;margin:0 auto;box-sizing:border-box;background-color:white}@media (max-width: 768px){.app{width:100%}}

.home__wrapper{display:flex;height:100vh;background-position:right 0 top;background-size:80% auto;background-repeat:no-repeat}.home__content{width:35%;color:#022a71;padding-left:20px}.home__content h1{font-size:48px}.home__content p{font-size:24px;font-family:"Poor Story",Arial,sans-serif}.home__content button{margin-top:40px;border-radius:30px;width:200px;height:50px;font-size:24px;outline:none;border:#a3c0f5;background-color:#a3c0f5;color:#022a71}@media (max-width: 575.98px){.home__wrapper{margin:0 20px}}@media (max-width: 767.98px){.home__wrapper{display:flex;height:100vh;margin:0 40px;background-position:center bottom 45%;background-size:90% auto;background-repeat:no-repeat}.home__content{width:100%;padding-left:0}.home__content h1{margin:2rem auto;font-size:24px;width:80%;text-align:center}.home__content p{margin:1rem auto;width:60%;text-align:center}.home__content button{position:absolute;bottom:20px;left:50%;margin-right:-50%;transform:translate(-50%, -50%);width:150px;height:35px;font-size:18px}}@media (max-width: 991.98px){.home__wrapper{background-size:80% auto}.home__content h1{margin:2rem auto;font-size:32px}.home__content button{width:150px;height:35px;font-size:18px}}

.logo{height:70px;width:auto}

.nav{display:flex;align-items:center;justify-content:space-around;margin-right:20px}@media (max-width: 768px){.nav{margin-right:5px}}.nav__home,.nav__practice,.nav__dictionary{display:flex;justify-content:center;align-items:center;width:100px;font-size:24px;margin:0 15px;font-weight:bold;text-decoration:none;color:#032c76}.nav__home.active,.nav__practice.active,.nav__dictionary.active{color:#f19307;border-radius:25px}

.header{display:flex;background-color:#ffffff;width:100%;height:70px;justify-content:space-between;border-bottom:1px solid #a3c0f5;font-family:'Poor Story', Arial, sans-serif}.header__nav{display:flex;justify-content:center;align-items:center}@media (max-width: 768px){.header__nav{display:none}}.header__burger-menu{display:none;justify-content:center;align-items:center}@media (max-width: 768px){.header__burger-menu{display:flex;margin-right:30px}}

.menu{display:flex;flex-direction:row;list-style-type:none;margin:0;padding:0}.menu>li{margin:0 1rem;overflow:hidden}.menu-button-container{display:none;height:100%;width:30px;cursor:pointer;flex-direction:column;justify-content:center;align-items:center}#menu-toggle{display:none}.menu-button,.menu-button::before,.menu-button::after{display:block;background-color:#022a71;position:absolute;height:4px;width:30px;transition:transform 400ms cubic-bezier(0.23, 1, 0.32, 1);border-radius:2px}.menu-button::before{content:'';margin-top:-8px}.menu-button::after{content:'';margin-top:8px}#menu-toggle:checked+.menu-button-container .menu-button::before{margin-top:0px;transform:rotate(405deg)}#menu-toggle:checked+.menu-button-container .menu-button{background:rgba(255,255,255,0)}#menu-toggle:checked+.menu-button-container .menu-button::after{margin-top:0;transform:rotate(-405deg)}@media (max-width: 768px){.menu-button-container{display:flex}.menu{z-index:100;position:absolute;top:0;margin-top:70px;left:0;flex-direction:column;width:100%;justify-content:center;align-items:center}#menu-toggle ~ .menu li{height:0;margin:0;padding:0;border:0;transition:height 400ms cubic-bezier(0.23, 1, 0.32, 1)}#menu-toggle:checked ~ .menu li{height:2.5em;padding:10px;transition:height 400ms cubic-bezier(0.23, 1, 0.32, 1)}#menu-toggle:checked ~ .menu li:last-child{padding-bottom:20px;box-shadow:0 10px 10px -5px rgba(0,0,0,0.5)}.menu>li{display:flex;justify-content:center;margin:0;padding:0.5em 0;width:100%;color:white;background-color:white}}

.dictionary__header{width:100%;height:70px;background-color:#c9daf8;display:flex;justify-content:space-around;align-items:center}@media (max-width: 768px){.dictionary__header{flex-direction:column;padding:20px 0;height:auto}}.dictionary__header button{border-radius:30px;border:white;background-color:white;box-shadow:1px 1px 1px #858b95;width:150px;height:45px;outline:none;color:#022797;font-family:'Poor Story', Arial, sans-serif;font-size:24px;cursor:pointer}.dictionary__header p{font-size:24px;color:#022797;text-shadow:1px 1px 1px #eceaea}.dictionary__header p span{font-family:'Poor Story', cursive;font-size:30px}.dictionary__modal_p{text-align:center;color:#064676;font-size:24px}.dictionary__main{width:70%;min-height:575px;margin:0 auto}@media (max-width: 768px){.dictionary__main{width:90%}}.dictionary__footer{width:100%;height:70px;background-color:#c9daf8;display:flex;justify-content:center;position:absolute}@media (max-width: 768px){.dictionary__footer{bottom:unset}}

.modal{display:block;position:fixed;z-index:1;left:0;top:0;width:100%;height:100%;overflow:auto;background-color:#000;background-color:rgba(0,0,0,0.4)}.modal__content{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);z-index:10;background-color:#fefefe;padding:20px;border:1px solid #888;border-radius:20px;box-shadow:2px 2px 10px grey;width:30%}@media (max-width: 768px){.modal__content{width:80%}}.modal__content span{color:#aaa;float:right;font-size:28px;font-weight:bold}.modal__content span:hover,.modal__content span:focus{color:black;text-decoration:none;cursor:pointer}.modal__content .modal__content_title{color:#032c76;font-size:30px;font-weight:300;display:flex;align-items:center;justify-content:center;font-family:'Poor Story', Arial, sans-serif}.modal__control{display:flex;justify-content:center;align-items:center;margin-top:20px}.modal__control_btn{margin-bottom:20px;width:25%;outline:none;border:1px solid orange;color:white;background-color:orange;border-radius:20px;cursor:pointer;height:30px;transition:30ms;font-size:20px;font-weight:bold;font-family:'Poor Story', Arial, sans-serif}.modal__control_btn:hover{transform:scale(1.1)}

.word__form{display:flex;justify-content:space-around;align-items:center;flex-direction:column}.word__form input{border-radius:5px;border:1px solid #d9dadb;outline:none;background-color:white;width:200px;height:40px;font-size:18px;padding-left:15px;box-shadow:0 0 5px #b1b2b3}.word__form input:last-child{margin-top:20px}

.word{display:flex;align-items:center;justify-content:space-around}.word__eng{width:35%;text-align:left;font-size:18px;color:#022a71;font-weight:bold}.word__rus{width:50%;font-size:18px}.word__del{width:20px;height:20px;background-color:#61a5c9;color:white;cursor:pointer;text-align:center;margin-right:20px}.word__line{border-bottom:1px solid #b8b5b5}

.words__wrapper{width:100%;margin:0 auto}

.search{border-radius:30px;border:white;background-color:white;box-shadow:1px 1px 1px #858b95;color:#2c2c2d;font-size:18px;padding:12px 15px}

.notification_wrapper{display:flex;justify-content:center;align-items:center;text-align:center;padding:5px;width:200px;height:50px;color:#ffffff;border-radius:5px;border:1px solid #f39c1b;background-color:#f39c1b;position:absolute;bottom:40px;right:20px}

.pages{display:flex;align-items:center;justify-content:center}.page{display:flex;justify-content:center;align-items:center;width:35px;height:35px;font-size:18px;margin:5px;border:1px solid #ffffff;border-radius:50%;outline:none;background-color:white;color:#032c76}.page.active{background-color:cornflowerblue;border:1px solid cornflowerblue;color:white}.page__link{text-decoration:none;color:#022a71}

.message{text-align:center;margin-top:100px;color:orange;font-size:40px;font-family:'Poor Story', Arial, sans-serif}

.input__wrapper{display:flex;justify-content:space-around;align-items:center;width:35%;height:100px;color:#032c76}.input__wrapper input{width:250px;height:50px;border:1px solid #dce5f6;border-radius:10px;background-color:#ebf0fa;color:#022a71;padding-left:15px;outline:none;font-size:30px}.input__wrapper input.mistake{border:1px solid #f11a1a;background-color:#f8eded}.btn__control{display:flex;justify-content:center;align-items:center;width:50px;height:50px;margin:0 5px;font-size:30px;font-weight:700;color:#6472a5;cursor:pointer}

.card{display:flex;justify-content:center;align-items:center;width:40%;height:270px;margin-top:25px;border:1px solid #f8f1e5;color:#032c76;box-shadow:inset 0 0 20px 10px #d7d6d4;border-radius:10px}@media (max-width: 768px){.card{width:80%}}.card p{font-size:45px}

.practice{display:flex;flex-direction:column;justify-content:center;align-items:center}.practice__switcher{width:100%;height:70px;display:flex;justify-content:flex-start;align-items:center}@media (max-width: 768px){.practice__switcher{justify-content:center}}

.switcher{display:inline-block;height:30px;margin:3px 10px;padding:4px;background-color:#fff;width:200px;border-radius:30px;border:solid 1px #ddd;position:relative}.switcher__input{display:none}.switcher__label{float:left;width:50%;font-size:14px;color:#032c76;line-height:30px;text-align:center;cursor:pointer;position:inherit;z-index:10}.switcher__toggle{position:absolute;float:left;height:30px;width:50%;font-size:12px;line-height:30px;cursor:pointer;background:#bacef3;border-radius:30px;box-shadow:0 4px 6px -1px rgba(0,0,0,0.1),0 2px 4px -1px rgba(0,0,0,0.06);left:5px;top:4px;transition:left 0.25s ease-out;will-change:transform}.switcher__input_rus:checked ~ .switcher__toggle{left:100px}

